import { connect } from 'react-redux';
import React, { useState, useRef } from 'react';
import './App.css';
import { Jumbotron  } from 'react-bootstrap';
import './Responsive.scss';
import { Link, useHistory } from 'react-router-dom';

function Category(props) {
    let history = useHistory();
  
    return (
        <div className="category" onClick={()=>{ return ( (props.isButton) ? history.push('./category/' + props.categoryData.id ) : null) }}>
            <h1 className='categoryTitle'>{props.categoryData.title}</h1>
            <p className='categoryContent'>{props.categoryData.content}</p>
        </div>
    )
  }
  
  function CategoryPage(props) {
  
    let categories = props.categoryData;
    
    const [startX, setStartX] = useState(0);
    const [isDrag, setIsDrag] = useState(false);
    const scrollRef = useRef(null);

    const [x, setX] = useState(0); let [y, setY] = useState(0);
    const [isButton, setIsButton] = useState(false);
  
  
    function onDragStart(e) {

        e.preventDefault();
        setIsDrag(true);
        setStartX(e.pageX + scrollRef.current.scrollLeft);

        setX(e.pageX); setY(e.pageY);
    }
  
    function onDragMove(e) {
        if (isDrag) {
            const { scrollWidth, clientWidth, scrollLeft } = scrollRef.current;

            scrollRef.current.scrollLeft = startX - e.pageX;

            if (scrollLeft === 0) {
                setStartX(e.pageX);
            } else if (scrollWidth <= clientWidth + scrollLeft) {
                setStartX(e.pageX + scrollLeft);
            }
        }
    }
  
    function onDragEnd(e) {
        const delta = 6;
        const diffX = Math.abs(e.pageX - x);
        const diffY = Math.abs(e.pageY - y);

        if(diffX < delta && diffY < delta){
            setIsButton(true);
        } else {
            setIsButton(false);
        }
        setIsDrag(false);
    }
  
    function onDragLeave() {
        setIsDrag(false);
    }
  
    return (
        <div>
            <Jumbotron className="background">
                <div className='categories'
                    onMouseDown={onDragStart}
                    onMouseMove={(isDrag) ? onDragMove : null}
                    onMouseUp={onDragEnd}
                    onMouseLeave={onDragLeave}
                    ref={scrollRef}
                >
                    {
                        categories.map(function (a, i) {

                            return <Category categoryData={a} isButton={isButton}/>
                        })
                    }
                </div>
            </Jumbotron>
        </div>
    )
  }
  
  function getData(state) {
    return {
      categoryData : state
    }
  }

export default (connect)(getData)(CategoryPage)